import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { useSelector } from 'react-redux';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { LandingContextsProvider } from '../UI';
import { PageHome, PageSetup, VisualBuilder, PageRouter } from './Page';

export const LandingsPageRouter = () => {
    const guideFinished = useSelector((state) => state.workspace.onboarding.editor.finished);
    const { website, page } = useWebsitePageBySlugs();
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });

    if (!page) {
        return null;
    }

    if (!guideFinished) {
        return <Redirect to={generateUrl('guide')} />;
    }

    return (
        <LandingContextsProvider base={`/pages/-/${website._id}/${page._id}`} website={website} page={page}>
            <Switch>
                <Route path="/pages/-/:websiteSlug/:pageSlug/setup" component={PageSetup} />
                <Route path="/pages/-/:websiteSlug/:pageSlug/edit" component={VisualBuilder} />
                <Route
                    path="/pages/-/:websiteSlug/:pageSlug"
                    render={() => (
                        <>
                            <PageHome />
                            <PageRouter />
                        </>
                    )}
                />
            </Switch>
        </LandingContextsProvider>
    );
};
