import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { matchPath, useLocation, useRouteMatch } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';
import { flatten } from 'lodash';

const join = (parent, child) => (parent && child) ? `${parent}/${child}` : child;

const mapChildRoute = function *(path, routeConfig) {
    if (__DEV__ && !routeConfig) {
        throw new Error(`Miss-configured route "${path}"`);
    }

    const { component, exact, strict, sensitive, children } = typeof routeConfig === 'function'
        ? { component: routeConfig }
        : routeConfig;

    if (component) {
        yield {
            exact: exact || !children,
            path,
            component,
            strict,
            sensitive,
        };
    }

    if (children) {
        for (const [childPath, config] of Object.entries(children)) {
            yield * mapChildRoute(join(path, childPath), config);
        }
    }
};

const mapRouting = (path, routing) =>
    flatten(
        Object.entries(routing)
            .map(([routePath, config]) => Array.from(mapChildRoute(join(path, routePath), config)))
    );

export const AnimateSwitch = ({ routing, absolute = true }) => {
    const { path } = useRouteMatch();
    const location = useLocation();
    const resolved = useMemo(() => mapRouting(path, routing), [absolute && path, routing]);

    return (
        <AnimatePresence>
            {resolved.map((route) => {
                const matched = matchPath(location.pathname, route);
                const Component = matched && route.component;

                return Component && <Component key={matched.path} {...matched.params} />;
            })}
        </AnimatePresence>
    );
};

const detailedLeafShape = {
    component: PropTypes.elementType.isRequired,
    exact: PropTypes.bool,
    strict: PropTypes.bool,
    sensitive: PropTypes.bool,
};
const detailedLeaf = PropTypes.shape(detailedLeafShape);
const leaf = PropTypes.oneOfType([detailedLeaf, PropTypes.elementType]);
const children = PropTypes.objectOf(leaf.isRequired);
detailedLeafShape.children = children;

AnimateSwitch.propTypes = {
    absolute: PropTypes.bool,
    routing: children.isRequired,
};
