import { useMemo } from 'react';
import { useRouteMatch } from 'react-router-dom';

const PREVIEW_PATH = '/pages/-/:any/:any/edit/preview';
const MOBILE_PATH = `${PREVIEW_PATH}/mobile`;

export const useBuilderModes = () => {
    const isViewMode = !!useRouteMatch(PREVIEW_PATH);
    const isMobileMode = !!useRouteMatch(MOBILE_PATH);
    const isDesktopPreviewMode = isViewMode && !isMobileMode;

    return useMemo(
        () => ({
            isViewMode,
            isMobileMode,
            isDesktopPreviewMode,
        }),
        [isViewMode, isMobileMode, isDesktopPreviewMode]
    );
};
