import React, { useCallback } from 'react';

import Select from 'react-select';

const selectThemeOverride = (theme) => ({
    ...theme,
    colors: {
        ...theme.colors,
        primary: '#007aff',
        neutral20: '#c7c7cc',
        neutral30: '#8e8e93',
    },
});

const filterValue = (multiple, options, value) =>
    multiple
        ? (value && value.length && options.filter((item) => value.includes(item.value))) || []
        : options.find((item) => item.value === value);

export function StyledSelectInput({ value, multiple, disabled, onChange, ...rest }) {
    const handleChange = useCallback((changed, { action, removedValue }) => {
        switch (action) {
            case 'remove-value':
            case 'pop-value':
                if (removedValue.isFixed) {
                    return;
                }

                break;
        }

        const normalized = multiple
            ? changed
                ? changed.map((item) => item.value)
                : []
            : changed?.value || undefined;

        onChange(normalized);
    }, [onChange, multiple]);

    return (
        <Select
            theme={selectThemeOverride}
            isClearable={false}
            isSearchable={false}
            isDisabled={disabled}
            isMulti={!!multiple}
            menuPlacement="auto"
            value={filterValue(multiple, rest.options, value)}
            onChange={handleChange}
            {...rest}
        />
    );
}

StyledSelectInput.propTypes = Select.propTypes;
