import PropTypes from 'prop-types';
import React, { useMemo, useRef } from 'react';

import { cls, getTransparentColor } from '@mssgme/helpers';
import { LinkBlockKind } from '@mssgme/shared';
import { LandingsPropTypes } from '@mssgme/ui';

import { useBlockTheme, useContrastingPageColor } from '../../hooks';
import { SmartButton } from '../../UI';

import styles from './PageMenu.scss';

export const PageMenu = ({ website, page, interactive = true, isViewMode = false, isMobileMode, ...rest }) => {
    const { color, backgroundColor } = useContrastingPageColor();
    const style = { backgroundColor: getTransparentColor(backgroundColor, 0.9).toRgbString(), color };
    const ref = useRef();
    const block = useBlockTheme({ kind: LinkBlockKind, theme: {} });
    const pageId = page._id;
    const links = useMemo(
        () => website.pages.map((page) => ({
            page: page._id,
            title: page.name,
            active: page._id === pageId,
        })),
        [website.pages, pageId]
    );

    if (website.pages.length <= 1) {
        return null;
    }

    // can't use css breakpoints here
    const isCompacted = isMobileMode || ((ref.current?.clientWidth || 0) < 600);

    return (
        <section
            key={pageId}
            ref={ref}
            id="page_menu"
            style={style}
            {...cls([styles.root, isCompacted && styles.compact, !interactive && styles.disabled], rest)}
        >
            <div className={styles.header}>
                {website.name}
            </div>

            <input id="menu-toggle" type="checkbox" className={styles.toggle} disabled={!interactive} />

            <label className={styles.buttonContainer} htmlFor="menu-toggle">
                <div className={styles.button}></div>
            </label>

            <ul className={styles.menu} style={{ backgroundColor: color }}>
                {links.map((link) => (
                    <li key={link.page} className={styles.item} style={style}>
                        <SmartButton
                            interactive={interactive}
                            isViewMode={isViewMode}
                            id={link.page}
                            link={link}
                            active={link.active}
                            width={block.theme.width}
                            icon={block.theme.icon}
                            urlSubtitle={block.theme.urlSubtitle}
                            style={isCompacted ? block.style : style}
                        />
                    </li>
                ))}
            </ul>
        </section>
    );
};

PageMenu.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    interactive: PropTypes.bool,
    isViewMode: PropTypes.bool,
    isMobileMode: PropTypes.bool,
};
