import React, { useMemo } from 'react';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { AnimateSwitch } from '@mssgme/ui';

import { WebsiteEditorContext } from '../../hooks';
import {
    Embed,
    HasAdvancedBlocksMessage,
    InstagramShareNew,
    NoBlocksMessage,
    NotPublishedMessage,
    PagesList,
    PagesMenu,
    Qr,
    ShareNew,
    SharePublished,
    Socials,
    TikTokShare,
} from '../Page';
import { TemplateCreatePage } from '../Template';
import { FeedbackDetails, FeedbackList } from '../Feedback';
import {
    WebsiteBrandingEdit,
    WebsiteDelete,
    WebsiteDomainEdit,
    WebsiteEmbeddedEdit,
    WebsiteLocaleEdit,
    WebsiteNameEdit,
    WebsitePublishSlugEdit,
    WebsiteSeoEdit,
    WebsiteStatusEdit,
    WebsiteTrackingPlatformEdit,
    WebsiteUrlEdit,
} from './Edit';
import { WebsiteAnalytics, WebsiteAudience, WebsiteSettings, WebsiteTracking } from './View';

const routing = {
    // setup: {
    //     exact: false,
    //     component: PageSetup,
    // },

    published: SharePublished,
    'not-published': {
        exact: true,
        component: NotPublishedMessage,
        children: {
            'no-blocks': NoBlocksMessage,
        },
    },
    'has-advanced-blocks': HasAdvancedBlocksMessage,

    ':share(share|published|published/share|share/share|standalone)': {
        component: ShareNew,
        children: {
            name: {
                component: WebsitePublishSlugEdit,
                children: {
                    domain: WebsiteDomainEdit,
                },
            },
            embed: Embed,
            qr: Qr,
            ':socials(socials|bio)': {
                component: Socials,
                children: {
                    instagram: InstagramShareNew,
                    tiktok: TikTokShare,
                },
            },
        },
    },

    settings: {
        component: WebsiteSettings,
        children: {
            tracking: {
                component: WebsiteTracking,
                children: {
                    ':platform': WebsiteTrackingPlatformEdit,
                },
            },
            seo: WebsiteSeoEdit,
            url: WebsiteUrlEdit,
            domain: WebsiteDomainEdit,
            name: WebsiteNameEdit,
            locale: WebsiteLocaleEdit,
            embedded: WebsiteEmbeddedEdit,
            status: WebsiteStatusEdit,
            branding: WebsiteBrandingEdit,
            'no-blocks': NoBlocksMessage,
            template: TemplateCreatePage,
            delete: WebsiteDelete,
        },
    },

    analytics: WebsiteAnalytics,

    audience: WebsiteAudience,

    feedback: {
        component: FeedbackList,
        children: {
            ':feedbackId': FeedbackDetails,
        },
    },

    list: {
        component: PagesList,
        children: {
            menu: PagesMenu,
        },
    },
};

export const WebsiteRouter = () => {
    const { websiteId } = useWebsitePageBySlugs();
    const context = useMemo(() => ({ websiteId, pageId: null, blockId: null }), [websiteId]);

    return (
        <WebsiteEditorContext.Provider value={context}>
            <AnimateSwitch routing={routing} />
        </WebsiteEditorContext.Provider>
    );
};
