import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { Button, ProtectAccess } from '@mssgme/ui';
import { usePagesFactory, useWebsiteBySlug } from '@mssgme/hooks';

export function CreatePageButton() {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);

    const { pending, create } = usePagesFactory(website._id);

    return (
        <ProtectAccess strict rule="pages:create">
            {(onClick) => (
                <Button
                    fullWidth
                    outline
                    isFetching={pending}
                    onClick={onClick || create}
                >
                    + {t('labels.page.createNew')}
                </Button>
            )}
        </ProtectAccess>
    );
}
