import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useHistory } from 'react-router-dom';

import { Button, ModalPane } from '@mssgme/ui';
import { useActions, useFetching, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';

export function PageDelete() {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { websiteId, pageId } = useWebsitePageBySlugs();
    const closePath = generateUrl('..');

    const { pending, run: handleDelete } = useFetching(async () => {
        await api.pages.delete({ websiteId, pageId });

        history.push(`/pages/-/${websiteId}/list`);
    }, [websiteId, pageId, history]);

    return (
        <ModalPane title={t('Page.Edit.Delete.heading')} onClose={closePath}>
            <p style={{ margin: 0, color: '#777', textAlign: 'center' }}>{t('Page.Edit.Delete.fieldTip')}</p>
            <Button tag={Link} to={generateUrl('..')} fullWidth style={{ marginTop: 16 }} btnStyle="secondary2">
                {t('labels.page.cancelDelete')}
            </Button>
            <Button fullWidth onClick={handleDelete} style={{ marginTop: 10 }} btnStyle="danger" isFetching={pending}>
                {t('labels.page.delete')}
            </Button>
        </ModalPane>
    );
}
