import { useMemo } from 'react';
import { map, groupBy } from 'lodash';

import { checkRestrictions } from '@mssgme/shared';
import { useWorkspace } from '@mssgme/hooks';

export function usePageCheck(page) {
    const workspace = useWorkspace();
    const blocks = page?.blocks;
    const theme = page?.theme;

    return useMemo(() => {
        if (!(blocks || theme)) return null;

        let warning;
        const kinds = [];
        const context = { workspace };

        checkRestrictions(context,  'page-blocks-publish', blocks, (error, block) => {
            if (error) {
                warning ||= error;
                kinds.push(block.kind);
            }

            return false;
        });

        if (warning) {
            warning.blocks = map(groupBy(kinds), (item) => ({
                kind: item[0],
                qty: item.length,
            }));

            return warning;
        }

        return checkRestrictions(context, 'page-themes:edit', theme);
    }, [workspace, blocks, theme]);
}
