import React from 'react';
import { Route, Switch } from 'react-router-dom';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { ProductsPage } from '@mssgme/products';
import { LandingContextsProvider } from '../UI';
import { EditRedirect } from './EditRedirect';
import { LandingsPageRouter } from './LandingsPageRouter';
import { WebsiteHome, WebsiteRouter } from './Website';

export const LandingsWebsiteRouter = () => {
    const { website, page } = useWebsitePageBySlugs();
    const ids = website.pages.map(({ _id }) => _id);

    return (
        <LandingContextsProvider base={`/pages/-/${website._id}`} website={website} page={page}>
            <Switch>
                <Route path="/pages/-/:websiteSlug/products" component={ProductsPage} />
                <Route path="/pages/-/:websiteSlug/edit" component={EditRedirect} />
                <Route
                    path="/pages/-/:websiteSlug/:pageSlug?"
                    render={({ match: { params: { pageSlug } } }) => {
                        if (pageSlug && ids.includes(pageSlug)) {
                            return <LandingsPageRouter />;
                        }

                        return (
                            <>
                                <WebsiteHome />
                                <WebsiteRouter />
                            </>
                        );
                    }}
                />
            </Switch>
        </LandingContextsProvider>
    );
};
