    [VideoGalleryEdit.block.kind]: VideoGalleryEdit,
    [FeaturesEdit.block.kind]: FeaturesEdit,
    [AccordionEdit.block.kind]: AccordionEdit,
    [ImageGalleryEdit.block.kind]: ImageGalleryEdit,
    [DividerEdit.block.kind]: DividerEdit,
    [HatEdit.block.kind]: HatEdit,
    [HeadingEdit.block.kind]: HeadingEdit,
    [ImageEdit.block.kind]: ImageEdit,
