export const useEditorsPreload = async () => {
    const { fulfilled } = useFetch(
        async () => Promise.all(Object.values(EDITORS_MAP).map(({ block }) => block.preload?.())),
        []
    );

    return fulfilled;
};

const curatePath = (url, path) => {
    const rootParts = url.split('/');
    const pathParts = path.split('/');

    let i = pathParts.length;
    const accumulator = [];

    while (i-- > 0) {
        const part = pathParts[i];

        accumulator.unshift([':kind', ':blockId'].includes(part) ? rootParts[i] : part);
    }

    return accumulator.join('/');
};

const matchRouteF = (location, to, fn) => {
    const matched = matchPath(location.pathname, to);

    return matched && fn(matched.path, matched.params);
};

const matchRoute = (location, to, Component) => {
    const matched = matchPath(location.pathname, to);

    return matched && <Component key={matched.path} {...matched.params} />;
};

const EditorsRouting = ({ store }) => {
    const location = useLocation();
    const workspace = useWorkspace();
    const website = useWebsiteData();
    const { path } = useRouteMatch();
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const [lastCollectionSlug, setLastCollectionSlug] = useState(false);
    const [firstLoad, setFirstLoad] = useState(true);
    const entries = Object.entries(EDITORS_MAP).map(([key, value]) => [toDashedCase(key), value]);
    const root = generateUrl('.');

    const curated = curatePath(root, path);
    const preview = `${curated}/preview/:previewDevice(desktop|mobile)`;
    const paths = (kind, sub = '') =>
        Array.isArray(sub)
            ? [].concat(...sub.map((item) => paths(kind, item)))
            : [`${curated}/add-block/${kind}${sub}`, `${curated}/block/${kind}/:blockId${sub}`];

    const productPaths = useMemo(
        () => ({
            path: [
                ...paths(':kind', ['/p/:productSlug', '/c/:collectionSlug/p/:productSlug']),
                `${preview}/c/:collectionSlug/p/:productSlug`,
                `${preview}/p/:productSlug`,
            ],
            exact: false,
        }),
        [preview]
    );

    const collectionsPaths = useMemo(
        () => ({
            path: [
                ...paths(':kind', '/c/:collectionSlug'),
                `${preview}/c/:collectionSlug`,
                `${preview}/c/:collectionSlug/p/:productSlug`,
            ],
            exact: false,
        }),
        [preview]
    );

    const cartPaths = useMemo(
        () => ({
            path: [
                `${preview}/orders/:orderId`,
                `${preview}/p/:productSlug/orders/:orderId`,
                `${preview}/c/:collectionSlug/p/:productSlug/orders/:orderId`,
            ],
            exact: true,
        }),
        [preview]
    );

    const editors = (
        <AnimatePresence>
            {matchRoute(location, { path: `${curated}/add-block`, exact: true }, ChooseBlock)}

            {entries.map(([kind, EditorComponent]) => matchRoute(location, paths(kind), EditorComponent))}

            <Route key="nested" exact={false} path={paths(':kind')} component={EditorsRouting} />

            {matchRouteF(location, `${curated}/products`, (path) => (
                <ProductsContextProvider key={path} base={root} websiteId={website._id}>
                    <Route key={path} component={ProductsPage} />
                </ProductsContextProvider>
            ))}

            {matchRoute(
                location,
                `${curated}/block/${kebabCase(ServicesGridBlockKind)}/:blockId/convert`,
                ConvertServices
            )}
        </AnimatePresence>
    );

    const products = (
        <AnimatePresence>
            {matchRouteF(location, collectionsPaths, (path, params) => {
                const collectionSlug = params.collectionSlug || lastCollectionSlug;
                const collection = collectionSlug && website.collections.find(({ slug }) => slug === collectionSlug);

                if (!collection) {
                    return null;
                }

                return (
                    <CollectionView
                        key={path}
                        workspaceId={workspace._id}
                        websiteId={website._id}
                        collection={collection}
                        firstLoad={firstLoad}
                        setFirstLoad={setFirstLoad}
                        setLastCollectionSlug={setLastCollectionSlug}
                    />
                );
            })}

            {matchRouteF(location, productPaths, (path, params) => (
                <ProductView
                    key={path}
                    workspaceId={workspace._id}
                    defaultCollectionId={website.defaultCollectionId}
                    lastCollectionSlug={lastCollectionSlug}
                    setLastCollectionSlug={setLastCollectionSlug}
                    interactive={!!store}
                    {...params}
                />
            ))}

            {matchRoute(location, `${preview}/user-orders/`, UserOrdersPage)}
            {matchRoute(location, cartPaths, UserOrderPage)}
            {matchRoute(location, cartPaths, OrderPage)}

            {matchRoute(location, `${preview}/orders/:orderId/payment/:status`, PaymentStatusPage)}
        </AnimatePresence>
    );

    return (
        <>
            {editors}
            {store ? <Provider store={store}>{products}</Provider> : products}
        </>
    );
};

EditorsRouting.propTypes = {
    store: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
};

const routing = {
    // setup: PageSetup,
    sort: SortHelp,
    help: Help,
    guide: GuideBuilder,
    published: SharePublished,
    // published: PublishedMessage,
    // 'not-published': NotPublishedMessage,
    'not-published/no-blocks': NoBlocksMessage,
    'has-advanced-blocks': HasAdvancedBlocksMessage,
    // 'settings/embedded': {
    //     component: WebsiteEmbeddedEdit,
    //     props: { backPath: '../..' },
    // },
    ':share(share|published|published/share|share/share)': {
        component: ShareNew,
        children: {
            name: {
                component: WebsitePublishSlugEdit,
                children: {
                    domain: WebsiteDomainEdit,
                },
            },
            embed: Embed,
            qr: Qr,
            socials: SocialsShare,
            bio: SocialsShareBio,
            ':socials(socials|bio)': {
                children: {
                    instagram: InstagramShareNew,
                    tiktok: TikTokShare,
                },
            },
        },
    },
    styling: {
        component: PageStyling,
        exact: true,
        children: {
            theme: PageTheme,
            alignment: PageStylingAlignment,
            'background-color': PageStylingBackgroundColor,
            'background-image': PageStylingBackgroundImage,
        },
    },
};

export const EditBlockSheetRouting = ({ store }) => {
    const isReady = useEditorsPreload();

    if (!isReady) {
        return;
    }

    return (
        <BlockEditContextProvider>
            <AnimateSwitch routing={routing} />

            <EditorsRouting store={store} />
        </BlockEditContextProvider>
    );
};

EditBlockSheetRouting.propTypes = {
    store: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
};
