import React from 'react';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { TextField } from '@mssgme/ui';
import { PageEditForm } from '../../../../UI';

export function PageSlugEdit() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteId, page } = useWebsitePageBySlugs();

    return (
        <PageEditForm title={t('Page.Edit.Slug.heading')} websiteId={websiteId} page={page} backPath={generateUrl('..')}>
            <TextField
                builderInput
                name="slug"
                beforeInput={t('common:label.host')}
                placeholder={t('Page.fields.slug.placeholder')}
                maxLength={50}
                autoCapitalize="off"
                controlProps={{ style: { textTransform: 'lowercase' } }}
            />
        </PageEditForm>
    );
}
