import cn from 'classnames';
import React from 'react';
import PropTypes from 'prop-types';

import styles from './DomainStatusMark.scss';

export const DomainStatusMark = ({ isNew, isReady, isWaitingSSl }) => {
    const hint = isNew
        ? 'Resolving domain...'
        : isReady
            ? 'Domain connected'
            : isWaitingSSl
                ? 'Generating SSL...'
                : null;

    return (
        <div className={styles.root}>
            <div
                className={cn({ [styles.mark]: true, [styles.waitingSSL]: isWaitingSSl, [styles.ready]: isReady })}
                title={hint}
            />
        </div>
    );
};

DomainStatusMark.propTypes = {
    isNew: PropTypes.bool,
    isReady: PropTypes.bool,
    isWaitingSSl: PropTypes.bool,
};
