import PropTypes from 'prop-types';
import React from 'react';

import { Button, LandingsPropTypes } from '@mssgme/ui';

import { usePublish } from '../../hooks';

export const PublishButton = ({ routes, website, page, ...rest }) => {
    const { pending, run: handlePublish } = usePublish(website, page, routes);

    if (website.enabled) {
        return null;
    }

    return (
        <Button onClick={handlePublish} isFetching={pending} {...rest} />
    );
};

PublishButton.propTypes = {
    routes: PropTypes.object,
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
};
