import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { FiToggleRight, FiTrash } from 'react-icons/fi';
import { MdTitle } from 'react-icons/md';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { AclMenuPane } from '@mssgme/ui';

import { usePublish } from '../../../../hooks';

const menu = (t, page, website, canPublish) => [
    {
        to: !website.enabled && !page.blocks.length ? 'no-blocks' : 'status',
        label: t('Page.View.Settings.items.status'),
        icon: FiToggleRight,
        preview: page.enabled ? t('ui:labels.yes') : t('ui:labels.no'),
        filter: (!website.scaffold && canPublish) || page.enabled,
        disabled: website.homePageId === page._id,
        'data-event': 'click-page-settings',
        'data-feature': 'status',
    },
    {
        to: 'name',
        label: t('Page.View.Settings.items.name'),
        icon: MdTitle,
        preview: page.name,
        disabled: website.homePageId === page._id,
        'data-event': 'click-page-settings',
        'data-feature': 'name',
    },
    {
        to: 'slug',
        label: t('Page.View.Settings.items.slug'),
        icon: MdTitle,
        preview: page.slug,
        disabled: website.homePageId === page._id,
        'data-event': 'click-page-settings',
        'data-feature': 'slug',
    },
    {
        to: 'delete',
        label: t('labels.page.delete'),
        labelColor: '#dc5960',
        icon: FiTrash,
        iconColor: '#dc5960',
        disabled: website.homePageId === page._id,
        'data-event': 'click-page-settings',
        'data-feature': 'delete',
    },
];
export function PageSettings() {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { canPublish } = usePublish(website, page);
    const items = useMemo(() => menu(t, page, website, canPublish), [t, website, page, canPublish]);

    return <AclMenuPane title={t('Page.View.Settings.heading')} items={items} />;
}
