import React, { useMemo } from 'react';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { AnimateSwitch } from '@mssgme/ui';

import { WebsiteEditorContext } from '../../hooks';
import { HasAdvancedBlocksMessage, PageSettings } from './View';
import { PageDelete, PageNameEdit, PageSlugEdit, PageStatusEdit } from './Edit';

const routing = {
    'has-advanced-blocks': HasAdvancedBlocksMessage,
    settings: {
        component: PageSettings,
        children: {
            slug: PageSlugEdit,
            name: PageNameEdit,
            status: PageStatusEdit,
            delete: PageDelete,
        },
    },
};

export const PageRouter = () => {
    const { websiteId, pageId } = useWebsitePageBySlugs();
    const context = useMemo(() => ({ websiteId, pageId, blockId: null }), [websiteId, pageId]);

    return (
        <WebsiteEditorContext.Provider value={context}>
            <AnimateSwitch routing={routing} />
        </WebsiteEditorContext.Provider>
    );
};
