import React from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils } from '@mssgme/hooks';
import { Badge, BlockListItemPreview, LandingsPropTypes } from '@mssgme/ui';
import { FileIcon, HomeIcon, MoreHorizontalIcon } from '@mssgme/icons';

PageItem.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
};

export function PageItem({ website, page }) {
    const { t } = useTranslation('ui');
    const { generateUrl, pathname } = useNavigationUtils();
    const isHomePage = website.homePageId === page._id;
    const isEnabled = isHomePage || page.enabled;

    return (
        <BlockListItemPreview
            iconSplit
            tag={Link}
            label={(
                <div style={{ display: 'flex', gap: 8, alignItems: 'center', justifyItems: 'start', width: '100%' }}>
                    <Badge dot badgeStyle={(isHomePage || page.enabled) ? 'success' : 'flat'}/>
                    {isHomePage ? t('ui:labels.homePage') : page.name}
                </div>
            )}
            iconColor={isEnabled ? '#333' : '#aeaeae'}
            to={generateUrl(`../${page._id}`)}
            labelIcon={isHomePage ? HomeIcon : FileIcon}
            rightIcon={
                <Link
                    to={{ pathname: generateUrl(`../${page._id}/settings`), state: { backPath: pathname } }}
                    style={{ alignItems: 'center', display: 'inline-flex' }}
                >
                    <MoreHorizontalIcon size={22} color="#333"/>
                </Link>
            }
            preview={isHomePage ? '/' : `/${page.slug}`}
        />
    );
}
