import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useParams } from 'react-router-dom';

import { useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { BlockListItemPreview, FloatingActions, ListNoStyle, StreamlinedScreen } from '@mssgme/ui';
import { ChevronRightIcon, ListIcon } from '@mssgme/icons';
import { CreatePageButton } from '../../../../UI';
import { PageItem } from './PageItem';

export function PagesList() {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const { generateUrl } = useNavigationUtils();
    const website = useWebsiteBySlug(websiteSlug);

    return (
        <StreamlinedScreen modal light fullHeight={false} backPath={generateUrl('..')} title={t('Page.View.List.heading')}>
            <StreamlinedScreen.Section top>
                <ListNoStyle gutter={8} style={{ width: '100%' }}>
                    {website.pages.map((page) => (
                        <PageItem key={page._id} website={website} page={page} />
                    ))}
                </ListNoStyle>
            </StreamlinedScreen.Section>

            <StreamlinedScreen.Section top={1}>
                {website.pages.length > 1 && (
                    <ListNoStyle gutter={8} style={{ width: '100%' }}>
                        <BlockListItemPreview
                            iconSplit
                            tag={Link}
                            to={generateUrl('menu')}
                            label={t('Page.View.List.customizeMenu')}
                            labelIcon={ListIcon}
                            rightIcon={<ChevronRightIcon />}
                        />
                    </ListNoStyle>
                )}
                <FloatingActions padded pullout>
                    <CreatePageButton />
                </FloatingActions>
            </StreamlinedScreen.Section>
        </StreamlinedScreen>
    );
}
