import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import TinyColor2 from 'tinycolor2';
import { Link as NavLink } from 'react-router-dom';

import { GenericIcon, LinkBlank } from '@mssgme/ui';
import { cls, ua, urlSafeComponent } from '@mssgme/helpers';
import { __t, prettifyUrl } from '@mssgme/shared';
import { useEventCallback } from '@mssgme/hooks';

import { useCoreContext, usePageMeta } from '../../hooks';

import styles from './SmartButton.scss';

export function SmartButton({
    id,
    link,
    interactive,
    active,
    isViewMode,
    // todo: handle drafts
    // eslint-disable-next-line no-unused-vars
    isWebsitePublished,
    width,
    icon,
    urlSubtitle,
    onClick,
    style,
    ...rest
}) {
    const { router } = useCoreContext();
    const { title = link.title, name, url, slug, _id: pageId } = usePageMeta(link.page, isViewMode) || link;
    const { iconStyle, buttonStyle } = useMemo(() => {
        const color = TinyColor2(style.backgroundColor)
            .darken(4)
            .toString();
        const backgroundColor = TinyColor2(style.backgroundColor)
            .setAlpha(0.5)
            .toString();

        return {
            iconStyle: { borderRight: `1px solid ${color}` },
            buttonStyle: active ? { ...style, backgroundColor } : style,
        };
    }, [style.backgroundColor, active]);
    const isInternal = !!pageId;
    const displayTitle = title || name || prettifyUrl(url);
    const Tag = interactive ? isInternal ? NavLink : LinkBlank : 'div';
    const attributes = interactive ? isInternal ? { to: router.toPage(slug) } : { href: url } : {};

    const handleClick = useEventCallback((e) => {
        if (!interactive) {
            return e.preventDefault();
        }

        if (onClick) {
            e.preventDefault();

            return onClick({ link });
        }

        if (ua.Android) {
            e.preventDefault();

            const { currentTarget } = e;
            const href = currentTarget.href;

            window.open(href);
        }
    });

    return (
        <div className={cn(styles.wrapper, styles[width], active && styles.active)}>
            <Tag
                data-element="custom-link"
                data-event-name={urlSafeComponent(url)}
                data-title={title || name || prettifyUrl(url)}
                data-id={id}
                id={id}
                style={buttonStyle}
                onClick={handleClick}
                {...attributes}
                {...cls([styles.root, icon && styles.hasIcon, 'link-button'], rest)}
            >
                {icon && (
                    <div className={styles.icon} style={iconStyle}>
                        <GenericIcon name={icon} size={20} />
                    </div>
                )}

                <div className={styles.content}>
                    <div>{displayTitle || __t('link_placeholder')}</div>
                    {urlSubtitle && <div className={styles.subTitle}>{prettifyUrl(url)}</div>}
                </div>
            </Tag>
        </div>
    );
}

SmartButton.propTypes = {
    id: PropTypes.string.isRequired,
    width: PropTypes.oneOf(['adaptive', 'full']),
    icon: PropTypes.string,
    urlSubtitle: PropTypes.bool,
    link: PropTypes.object.isRequired,
    style: PropTypes.object.isRequired,
    active: PropTypes.bool,
    interactive: PropTypes.bool,
    isViewMode: PropTypes.bool,
    isWebsitePublished: PropTypes.bool,
    className: PropTypes.string,
    onClick: PropTypes.func,
};
