import React, { useContext, useMemo } from 'react';

import { isMatchId } from '@mssgme/helpers';
import { CRM_IDENTITY } from '../../products';

export const WebsiteDataContext = React.createContext({
    _id: '',
    workspace: '',
    name: '',
    slug: '',
    absoluteUrl: '',
    finalUrl: '',
    homePageId: '',
    collections: [],
    pages: [],
    seo: {},
    localization: {},
    crm: CRM_IDENTITY,
});

export const useWebsiteData = () => useContext(WebsiteDataContext);

export const usePageMeta = (pageId, isViewMode) => {
    const website = useContext(WebsiteDataContext);
    const page = pageId && website.pages.find(isMatchId(pageId));

    return useMemo(
        () => {
            if (!page) {
                return;
            }

            const isHomePage = website.homePageId === page._id;
            const slug = isHomePage ? '' : page.slug;

            return {
                ...page,
                name: (isHomePage ? website.name : page.name) || page.slug,
                url: website.finalUrl + (slug ? '/' + slug : '') + (page.draft ? `?draft=${page.draft}` : ''),
                slug: isViewMode ? pageId : slug,
            };
        },
        [website, page, isViewMode]
    );
};
