import { useCallback } from 'react';
import { useHistory } from 'react-router-dom';

import { clearWebsitePayload, useActions, useFetching, useNavigationUtils } from '@mssgme/hooks';
import { usePageCheck } from './usePageCheck';

// eslint-disable-next-line jsdoc/require-returns
/**
 * @param {*} website Website
 * @param {*} page Page
 * @param {{published: string}} [routes] Custom routes to navigate to
 */
export const usePublish = (website, page, routes) => {
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { api } = useActions();
    const history = useHistory();
    const warning = usePageCheck(page);
    const successPath = { pathname: generateUrl(routes?.published || '.'), state: { from: 'publishedMessage' } };

    const handleSuccess = useCallback(() => history.push(successPath), [successPath]);

    const handlePublish = useFetching(async () => {
        if (page.blocks.length) {
            if (warning) {
                return history.push(generateUrl('has-advanced-blocks'));
            }

            await api.websites.update({
                websiteId: website._id,
                payload: { ...clearWebsitePayload(website), enabled: true, scaffold: false },
            });

            handleSuccess();
        } else {
            history.push(`/pages/-/${website._id}/${page._id}/not-published/no-blocks`);
        }
    }, [website, page, handleSuccess, warning]);

    return {
        ...handlePublish,
        canPublish: !warning,
    };
};
