import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useHistory } from 'react-router-dom';
import { FiArrowLeft, FiShare } from 'react-icons/fi';

import { useNavigationUtils } from '@mssgme/hooks';
import { LandingsPropTypes, SelectPageInput, useTitle } from '@mssgme/ui';

import { PublishButton } from '../../../../UI';

import styles from './BuilderHeader.scss';

const pageSelectStyles = {
    control: (base) => ({ ...base, border: 'none', background: 'none', boxShadow: 'none' }),
    valueContainer: (base) => ({ ...base, paddingRight: 0 }),
    indicatorSeparator: (base) => ({ ...base, display: 'none' }),
    menu: (base) => ({ ...base, width: 'max-content', minWidth: '100%' }),
};

export function BuilderHeader({ isViewMode, isSortMode, website, page }) {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const isPublished = website.enabled;
    const isEmptyWebsite = !website.pages.some(({ blocks }) => blocks.length > 0);

    const handleSelectPage = useCallback((pageId) => history.push(generateUrl(`../../${pageId}/edit`)), [generateUrl]);

    useTitle(page.name);

    return (
        <div className={cn(styles.root, isViewMode && styles.viewMode, isSortMode && styles.sortMode)}>
            <div className={styles.container}>
                <div className={styles.row}>
                    <Link className={styles.backButton} to={generateUrl('../../list')}>
                        <FiArrowLeft size={20} color="#333" />
                    </Link>

                    <div className={styles.title}>
                        <SelectPageInput
                            styles={pageSelectStyles}
                            menuPosition="fixed"
                            menuPortalTarget={document.body}
                            isClearable={false}
                            website={website}
                            value={page._id}
                            onChange={handleSelectPage}
                        />
                    </div>

                    {!isEmptyWebsite && (
                        <>
                            {isPublished && (
                                <Link
                                    to={generateUrl('share')}
                                    className={styles.button}
                                    title={t('Editor.Dashboard.header.share')}
                                >
                                    <FiShare size={20} color="#333" />
                                </Link>
                            )}

                            {/*<Link*/}
                            {/*    to={generateUrl(desktopPreview ? 'preview/desktop' : 'preview')}*/}
                            {/*    className={styles.button}*/}
                            {/*    title={t('Editor.Dashboard.header.preview')}*/}
                            {/*>*/}
                            {/*    <FiEye size={20} color="#333" />*/}
                            {/*</Link>*/}

                            <PublishButton
                                size="small"
                                btnStyle="primary"
                                style={{
                                    userSelect: 'none',
                                    marginRight: 10,
                                }}
                                routes={{ published: '../published' }}
                                website={website}
                                page={page}
                                data-button="publish"
                            >
                                {t('Editor.Dashboard.header.publish')}
                            </PublishButton>
                        </>
                    )}
                </div>
                <div className={styles.row}>
                    <span>{t('Editor.Dashboard.header.sortTitle')}</span>
                </div>
            </div>
        </div>
    );
}

BuilderHeader.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    isSortMode: PropTypes.bool,
    isViewMode: PropTypes.bool,
};
