import React, { useContext } from 'react';
import { useMount, useUnmount } from 'react-use';

import { removeTrailingSlash } from '@mssgme/shared';

const joinPath = (...parts) => {
    return removeTrailingSlash(parts.reduce((acc, part) => {
        if (part ?? false) {
            return acc + '/' + removeTrailingSlash(part, { lead: true });
        }

        return acc;
    }, ''));
};

export const makeCoreRouter = ({ pageSlug, path, isBackOverriddenBack }) => {
    const toAbsolutePath = (pageSlug, ...parts) => joinPath(...parts).replace(':pageSlug', pageSlug);
    const entry = toAbsolutePath(pageSlug, path);

    return {
        toEntry: () => entry,
        toHome: () => entry,
        toPage: (pageSlug) => toAbsolutePath(pageSlug, path),
        product: {
            toView: (productSlug = ':productSlug') => `${entry}p/${productSlug}`,
        },
        orders: {
            toView: (orderId = ':orderId', basePath) =>
                `${basePath ? toAbsolutePath(pageSlug, basePath) : entry}/orders/${orderId}`,
            toStatus: (orderId = ':orderId', status = ':status(paid|failed)') =>
                `${entry}/orders/${orderId}/payment/${status}`,
        },
        userOrders: {
            toOrder: (orderId = ':orderId') => `${entry}/user-orders/${orderId}`,
        },
        isBackOverriddenBack,
    };
};

export const CoreContext = React.createContext({
    path: '/',
    router: makeCoreRouter({ path: '/' }),
    setEntry: () => {},
});

export const useBackCorePath = (backUrl) => {
    const { setEntry } = useCoreContext();

    useMount(() => setEntry(backUrl));
    useUnmount(() => setEntry());
};

export const useCoreContext = () => useContext(CoreContext);
