import React from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { Button, ProtectAccess } from '@mssgme/ui';

export function CreateWebsiteButton() {
    const { t } = useTranslation('landings');

    return (
        <ProtectAccess strict rule="websites:create">
            {(onClick) => (
                <Button
                    fullWidth
                    rounded
                    style={{
                        height: 56,
                        fontSize: 16,
                        lineHeight: '24px',
                        boxShadow: '1px 2px 6px rgba(0, 0, 0, 0.08)',
                    }}
                    tag={Link}
                    to="/pages/create"
                    onClick={onClick}
                >
                    + {t('labels.website.createNew')}
                </Button>
            )}
        </ProtectAccess>
    );
}
