import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useMeasure } from 'react-use';

import { cls } from '@mssgme/helpers';

import styles from './Scalable.scss';

export const Scalable = ({ isScaled, isAnimated, scale = 1, children, ...rest }) => {
    const actualScale = isScaled ? scale : 1;

    const [ref, { height }] = useMeasure();

    const style = useMemo(() => {
        const transform = `scale(${actualScale})`;
        const marginBottom = -((height / actualScale) * (1 - actualScale));

        return { transform, marginBottom };
    }, [height, actualScale]);

    return (
        <div
            ref={ref}
            style={style}
            {...cls([styles.root, !!height && styles.ready, isAnimated && styles.animated], rest)}
        >
            {children}
        </div>
    );
};

Scalable.propTypes = {
    isScaled: PropTypes.bool,
    isAnimated: PropTypes.bool,
    scale: PropTypes.number,
};
